import React, {useState} from 'react';
import handleSetResource from "../../services/handleSetResource";
import guid from '../../utils/guid';
import { BlipLoading } from '../BlipLoading';
import {NotificationContainer,NotificationManager} from 'react-notifications';
import { showToast } from "../../api/commomService"

function AnswerResource(){
  const [chaveRecurso, setChaveRecurso] = useState('');
  const [mensager, setMensager] = useState('');
  const [loading, setLoading] = useState(false);


  const handleRecordResource = async () => {
    setLoading(true);
    const getKey = localStorage.getItem('keyTwo');
    const result = await handleSetResource(guid(), getKey, "text/plain", chaveRecurso, mensager);

    if(result.data.status === 'success'){
      showMessage('Resource successfully registered!','success', 'Done!');
      return false; 
        
    } else {
      showMessage('Error registering a resource!','danger', 'Error!');
      return false;    
    }
  };

  
  function showMessage(msg, type, status) {

    setTimeout(() => {
      
        showToast({
          type: type,
          message: msg
      })
      setLoading(false);
     }, 900);
  
  }
  
  const handleResourceKey = (e) => {

    console.log(e)
    const {value} = e.target;
    setChaveRecurso(value)
  }
  

  const handleMensager = (e) => {
    const {value} = e.target;
    setMensager(value);
  }
 
 
  return (
    <div className="container w-100">

    <h1 >Reply Registration</h1>
    <label>Resource Key: </label>
    <div className="row mb-3">
      <div className="col-9">
        <input
                name="chave"
                className="bp-input-wrapper blip-input small "
                placeholder="Enter Resource Key"
                value={chaveRecurso}
                onChange={e => handleResourceKey(e)}
              />
      </div>
      
    </div>
     
        <div className="mt-2 mb-2">
          <div className="row">
            <div className="col-12">
              <label>Message:</label>  
            </div>   
            <div className="col-12">
              <textarea 
              className="bp-textarea-wrapper blip-textarea w-100"
              rows="8"
              name="estadoDestino"
              placeholder="Enter your message"
              value={mensager}
              onChange={e => handleMensager(e)}

              />
            </div>           
        </div>
        <hr />
      </div>
  
    <div className="mt-5 col-12">
      <button onClick={handleRecordResource}  className="bp-btn bp-btn--bot bp-btn--small">
        {loading && (
          <BlipLoading/>
        )}
        {loading && <span>Save</span>}
        {!loading && <span>Save</span>}
      </button>
    </div>
    <NotificationContainer/>
  </div>
  );

}

export default AnswerResource;