import React, {useState} from 'react';
import handleSetResource from "../../services/handleSetResource";
import guid from '../../utils/guid';
import { BlipLoading } from '../BlipLoading';
import {NotificationContainer} from 'react-notifications';
import { showToast } from "../../api/commomService"

function ConfigResource(){

  const [chaveRecurso, setChaveRecurso] = useState('');
  const [targetState, setTargetState] = useState('');
  const [menu, setMenu] = useState(true);
  const [valida, setValida] = useState(true);
  const [loading, setLoading] = useState(false);


  const handleRecordResource = async () => {
    
    setLoading(true);
    const getKey = localStorage.getItem('keyTwo');
    const config = {
      config: {
        valida,
          nextStep: {
              estadoDestino: targetState,
               menu
              }
            }
  }


  const result = await handleSetResource(guid(), getKey, "application/json", chaveRecurso, config)
  
  if(result.data.status === 'success'){
    showMessage('Resource successfully registered!','success', 'Done!');
  
    return false;    
  } else {
    showMessage('Error registering a resource!','danger', 'Error!');
   
    return false;    
  }
};


function showMessage(msg, type, status) {

  setTimeout(() => {
    
      showToast({
        type: type,
        message: msg
    })
    setLoading(false);
   }, 900);

}


  const handleTargetState = (e) => {
  
   const {value} = e.target;
   setTargetState(value);
   

  }

  const handleMenu = (e) => {

    const {value} = e.target;
    const  isTrueSet = (value === 'true');
    setMenu(isTrueSet);

  }

  const handleValida = (e) => {

    const {value} = e.target;
    const  isTrueSet = (value === 'true');
    setValida(isTrueSet);

  }

  const handleResourceKey = (e) => {

    console.log(e)
    const {value} = e.target;
    setChaveRecurso(value)
  }
 
 
  return (
    <div className="container w-100">

    <h1>Configuration Registration</h1>
    <label>Resource Key: </label>
    <div className="row mb-3">
      <div className="col-9">
        <input
                name="chave"
                className="bp-input-wrapper blip-input small disabled"
                placeholder="Enter Resource Key"
                value={chaveRecurso}
                onChange={e => handleResourceKey(e)}
                />
      </div>
      
    </div>
     
        <div className="mt-2 mb-2">
          <div class="row">
   
            <div class="col-3">
              <label>Destination State:</label>
              <input
                className="bp-input-wrapper blip-input small "
                name="estadoDestino"
                 placeholder="Enter the Destination state"
                value={targetState}
                onChange={e => handleTargetState(e)}
              />
            </div>
            <div class="col">
              <label>Menu Type:</label>              
              <select name="menu"  className="bp-input-wrapper blip-select small"  onChange={e => handleMenu(e)}>
                <option value="true">true</option>
                <option value="false">false</option>
              </select>
            </div>
            <div class="col">
              <label>Validate answer:</label>              
              <select name="valida"  className="bp-input-wrapper blip-select small" onChange={e => handleValida(e)}>
                <option value="true">true</option>
                <option value="false">false</option>
              </select>
            </div>
           
        </div>
        <hr />
      </div>
  
      <div className="mt-5 col-12">
        <button onClick={handleRecordResource}  className="bp-btn bp-btn--bot bp-btn--small">
              {loading && (
                <BlipLoading/>
              )}
              {loading && <span>Save</span>}
              {!loading && <span>Save</span>}
            </button>
      </div>
      <NotificationContainer/>
  </div>
  );

}


export default ConfigResource;