import React, { useState, useEffect, useCallback } from 'react';
import 'react-notifications/lib/notifications.css';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import {NotificationContainer,NotificationManager} from 'react-notifications';
import {BlipLoading}from '../BlipLoading'



// builder
import handleGetWorkFlow from '../../services/handleGetWorkFlow';
import handleSetPublishedFlow from '../../services/handleSetPublishedFlow';

import { showToast } from "../../api/commomService"

import guid from '../../utils/guid';
import 'react-notifications/lib/notifications.css';
import constante from '../../constante';


const useStyles = makeStyles((theme) => ({
  root: {
    margin: 'auto',
    marginTop: '-35px',
    marginButton:'10px', 
    marginLeft: '-30px'


  },
  cardHeader: {
    padding: theme.spacing(1, 2),
    fontFamily: 'Nunito Sans, Helvetica, sans-serif'
 
  },
  list: {
  
    width: 300,
    height: 180,
    backgroundColor: theme.palette.background.paper,
    maxHeight: 200,
    overflow: 'auto'
    
   
    
  },
  button: {
    margin: theme.spacing(0.5, 0),
  }
}));



function not(a, b) {

  return a.filter((value) => b.indexOf(value) === -1);
}

function intersection(a, b) {
  return a.filter((value) => b.indexOf(value) !== -1);
}

function union(a, b) {
  return [...a, ...not(b, a)];
}

function  TransferList() {

  
useEffect(() => {
  handleAddBoxInBuilder();
},[])

  const classes = useStyles();

  const [checked, setChecked] = useState([]);
  const [left, setLeft] = useState([]);
  const [right, setRight] = useState([]);

  const [keyOrigin, setKeyOrigin] = useState(constante.botMarketPlace);
  const [keyOrigin2, setKeyOrigin2] = useState();
  const [keyTarget, setKeyTarget] = useState(localStorage.getItem('keyTarget'));
  const [filter, setFilter] = useState('');
  const [leftAux, setLeftAux]= useState([])
  const [builderOrigin, setBuilderOrigin] = useState();
  const [builderTarget, setBuilderTarget] = useState();
  const [loading, setLoading] = useState(false)
  const leftChecked = intersection(checked, left);
  const rightChecked = intersection(checked, right);

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  const numberOfChecked = (items) => intersection(checked, items).length;

  const handleToggleAll = (items) => () => {
    if (numberOfChecked(items) === items.length) {
      setChecked(not(checked, items));
    } else {
      setChecked(union(checked, items));
    }
  };

  const handleCheckedRight = () => {
    setRight(right.concat(leftChecked));
    setLeft(not(left, leftChecked));
    setChecked(not(checked, leftChecked));
  };

  const handleCheckedLeft = () => {
    setLeft(left.concat(rightChecked));
    setRight(not(right, rightChecked));
    setChecked(not(checked, rightChecked));
  };

  const handleFilter = (e) => {

    const {value} = e.target;
    setFilter(value);

    setLeft(leftAux.filter((e) => e.boxText.includes(filter.trim())));

  }

  const handleKey = (e) => {

    const {value} = e.target;
    setKeyOrigin2(value);
    localStorage.setItem('market-key', keyOrigin2);
  }



  const handleAddBoxInBuilderButton = async () => {



    const comeBackHandleGetWorkFlowOrigin =  await  handleGetWorkFlow(guid(), keyOrigin2);

    setBuilderOrigin(comeBackHandleGetWorkFlowOrigin);
   

    const arrayOfBoxId = Object.keys(comeBackHandleGetWorkFlowOrigin.data.resource).filter((e) =>  e !== 'fallback' && e !== 'error');
   
    const listBox = []
    
     arrayOfBoxId.forEach((e) => {

      listBox.push({
               boxId: comeBackHandleGetWorkFlowOrigin.data.resource[e].id,
               boxText: comeBackHandleGetWorkFlowOrigin.data.resource[e].$title
       })
     });

     setLeft(listBox)
     setLeftAux(listBox)
    
    
}



  const handleAddBoxInBuilder = async () => {


      const comeBackHandleGetWorkFlowOrigin =  await  handleGetWorkFlow(guid(), keyOrigin);

      setBuilderOrigin(comeBackHandleGetWorkFlowOrigin);
     
  
      const arrayOfBoxId = Object.keys(comeBackHandleGetWorkFlowOrigin.data.resource).filter((e) =>  e !== 'fallback' && e !== 'error');
     
      const listBox = []
      
       arrayOfBoxId.forEach((e) => {

        listBox.push({
                 boxId: comeBackHandleGetWorkFlowOrigin.data.resource[e].id,
                 boxText: comeBackHandleGetWorkFlowOrigin.data.resource[e].$title
         })
       });

       setLeft(listBox)
       setLeftAux(listBox)
      
  
  }


const cloneBoxId = async () => {

  setLoading(true)

  if(localStorage.getItem('keyTarget') === ''){
    showMessage('Please check the destination key field!','warning', 'Notice!');
    return false;
  
  }


  const key = await localStorage.getItem('keyTarget');

  const comeBackHandleGetWorkFlowTarget =  await  handleGetWorkFlow(guid(), key);
  const comeBackHandleGetWorkFlowOrigin =  await  handleGetWorkFlow(guid(), keyOrigin);


 const flowTarget = comeBackHandleGetWorkFlowTarget
 const flowOrigin = comeBackHandleGetWorkFlowOrigin
 const boxSelected = right;

 console.log(flowTarget)


boxSelected.forEach((flow) => {

  flowTarget.data.resource[flow.boxId] = flowOrigin.data.resource[flow.boxId]

})


  
const publish  = await handleSetPublishedFlow(flowTarget.data.resource, key);


   if (publish.status === 200) {
     
     showMessage('Your box has been cloned successfully','success','Done!');
     handleAddBoxInBuilder();
     setRight([]);
     setLoading(false)
  
   } else {
    setLoading(false)
     showMessage('Error cloning your box','danger','Error!');
     
   }

}



const showMessage = (msg, type, status) => {

  setTimeout(() => {
     setBuilderOrigin([]);
     setBuilderTarget([]);
     showToast({
      type: type,
      message: msg
  })
   }, 900);

}



  const customList = (title, items) => (

    <Card>
      <CardHeader
        className={classes.cardHeader}
        
        avatar={
          <Checkbox
            onClick={handleToggleAll(items)}
            checked={numberOfChecked(items) === items.length && items.length !== 0}
            indeterminate={numberOfChecked(items) !== items.length && numberOfChecked(items) !== 0}
            disabled={items.length === 0}
            inputProps={{ 'aria-label': 'all items selected' }}
            color="primary"
          />
        }
        title={title}
        subheader={`${numberOfChecked(items)}/${items.length} selected`}
      />
      <Divider />
      <List className={classes.list}  dense component="div" role="list">
        {items.map((value) => {
          const labelId = `transfer-list-all-item-${value.boxId}-label`;

          return (
            <ListItem key={value.boxId}  role="listitem" button onClick={handleToggle(value)} >
              
              <ListItemIcon >
                <Checkbox
                  checked={checked.indexOf(value) !== -1}
                  tabIndex={-1}
                  disableRipple
                  inputProps={{ 'aria-labelledby': labelId }}
                  color="primary"
                />
              </ListItemIcon>
              <ListItemText   primaryTypographyProps={{style: {fontSize: 14}}} id={labelId} primary={`${value.boxText}`} />
            </ListItem>
          );
        })}
        <ListItem />
      </List>
    </Card>
  );

  const Buttons = () => (
    
    <div className="container-buttons-marketplace">
    
    <button 
    type="submit" 
    onClick={cloneBoxId} 
    style={{marginLeft:"30px", width:'228px'}} 
    className="bp-btn bp-btn--bot bp-btn--small">
            {loading && (
                <BlipLoading/>
                )}
                {loading && <span>Cloning</span>}
                {!loading && <span>Clone</span>}

    </button>

    <button 
    type="submit" 
    onClick={handleAddBoxInBuilderButton} 
    style={{marginLeft:"30px", width:'228px'}} 
    className="bp-btn bp-btn--bot bp-btn--small">
            {loading && (
                <BlipLoading/>
                )}
                {loading && <span>Get Builder</span>}
                {!loading && <span>Get Builder</span>}

    </button>
 
   </div>
  )

  return (


    
  
     <div>

<div className="container w-100">
      <div className="row">
        <div className="col-6">
          <label>bot key: </label>
          <input  value={keyOrigin2} 
                onChange={(e) => handleKey(e)}
                type="text" name="origem-key" className="bp-input-wrapper blip-input w-90"   
                placeholder="key" required/>
          </div>
          <div className="col-6">
          <label>State filter: </label> 
          <input  value={filter} 
                onChange={(e) => handleFilter(e)}
                type="text" name="origem" className="bp-input-wrapper blip-input w-90"   
                placeholder="State filter" required/>
          </div>
      
        </div>
      </div><br/><br/>



   
    <Grid container spacing={2} justifyContent="center" alignItems="center" className={classes.root}>



      <Grid item>
     
        {customList('State Choices', left)}
        </Grid>
      <Grid item>
        <Grid container direction="column" alignItems="center">
          <Button
            variant="outlined"
            size="small"
            className={classes.button}
            onClick={handleCheckedRight}
            disabled={leftChecked.length === 0}
            aria-label="move selected right"
          >
            &gt;
          </Button>
          <Button
            variant="outlined"
            size="small"
            className={classes.button}
            onClick={handleCheckedLeft}
            disabled={rightChecked.length === 0}
            aria-label="move selected left"
          >
            &lt;
          </Button>
        </Grid>
      </Grid>
      <Grid item>{customList('State Chosen', right)}</Grid>
    </Grid>
   <Buttons/>
   </div>
  );


}

const  MarketPlace = () => {

  const [keyTarget, setKeyTarget] = useState(localStorage.getItem('keyTarget'));

  const setLocaStorageKey1 = (e) => {
    setKeyTarget(e)
    localStorage.setItem('keyTarget', e )

 }


    const page = (
     
      <div className="fadeIn animated" style={{padding:'30px'}}>
        <div className="home">
        </div>
  
      </div>
       
     
    )

    return (
      <div className="">
        {page}
        <TransferList/>
        <NotificationContainer/>
      </div>
    );

  }


export default MarketPlace;
