import React, {useState} from 'react';
import {BlipSelect} from '../BlipSelect/index'

import Answer from '../BlipAnswerResource/answerResouce';
import  MenuResource  from '../BlipMenuResource/menuResource';
import  ConfigResource  from '../BlipConfigResource/configResource';
function Resource(){

  const [config, setConfig] = useState(false);
  const [key, setKey] = useState(localStorage.getItem('keyTwo') || '');

  const data = [
    { value: 'resposta', label: 'Resposta' },
    { value: 'config', label: 'Config' },
    { value: 'menu', label: 'Menu' },
  ];

  const handleSetKeyLocalStorage = (e) => {
    const {value} = e.target;
    localStorage.setItem("keyTwo", value);
    setKey(value);
  };

  return(
    
    <>
    <div className="container w-100">
      <div className="row">
        <div className="col-6">
          <label>bot key: </label>
          <input
                  name="key"
                  className="bp-input-wrapper blip-input w-100"
                  placeholder="Digite Key do bot"
                  value={key}
                  onChange={e => handleSetKeyLocalStorage(e)}
                  required disabled={true}
                />
          </div>
          <div className="col-6">
          <label>Select a Resource: </label> 
            <BlipSelect options={data} onSelectOption={($event) => setConfig($event['$event'].optionProps.value)}/>
          </div>
          <div className="w-100">
              {
              config === 'resposta' ? <Answer/> : config === 'config' ? <ConfigResource/> : config === 'menu' ? <MenuResource/> :''
              }
          </div> 
        </div>
      </div>
    </>
      
  )

}


export default Resource;