import React, { useState } from 'react';
import {NotificationContainer} from 'react-notifications';
import handleGetWorkFlow from '../../services/handleGetWorkFlow';
import handleSetPublishedFlow from '../../services/handleSetPublishedFlow';


import handleGetResourceAll from '../../services/handleGetResourceAll';
import handleGetResourceItems from '../../services/handleGetResourceItems';


import { showToast } from "../../api/commomService"
import guid from '../../utils/guid';
import 'react-notifications/lib/notifications.css';

import ReactJson from 'react-json-view'
import { BlipLoading } from '../BlipLoading';

function Clone(){

  
  const [keyOrigin, setKeyOrigin] = useState(localStorage.getItem('keyTwo'));
  const [loading, setLoading] = useState(false);
  const [flow, setFlow] = useState([]);
  const [flowCopy, setFlowCopy] = useState([]);


 async function start(event) {

   setLoading(true);
   event.preventDefault();

   if(!keyOrigin){
     showMessage('Data is missing. Please check the Source and Destination Keys!!','warning', 'Notice!');
     return false;
   
   }
     
   if (!keyOrigin.includes('Key')) {
     showMessage('key typed, is not a valid key!', 'warning', 'Notice!');
     return false;
   } 



 const flow1 = {
   "respostas": []
 }


   const {data:{resource}} = await handleGetResourceAll(guid(), keyOrigin);

        resource.items.forEach(async (estado) => {
 
         
           const {data:{resource:{config, menu}, type}} = await handleGetResourceItems(guid(), keyOrigin, estado);

             
             const flowState = {
               "estado": estado,
               "inputs": !menu ? [] : menu.map((e, index) => `${index = index+1}` ),
               "inputText": !menu ? [] : menu.map((e, index) => `${e.opcao}-${index = index+1}`) ,

               "resposta": menu ? menu.map((e, index) => {
           
                 return {
                  "id": `${index = index+1}`,
                  "resposta": e.menu === false ? `{{resource.${e.estadoDestino}_resposta}}`: '' ,
                  "tracking": `${estado}|${e.opcao}`,
                  "time": "",
                  "tipo": e.menu === true ? 'application/vnd.lime.select+json': 'text/plain' ,
                  "cabecalho": e.menu === true ? `{{resource.${e.estadoDestino.toLowerCase()}_resposta}}`: '',
                  "opcoesDeMenu": e.menu === true ? `{{resource.${e.estadoDestino}}}` : '',
                  "estadoDestino": e.menu === true ? e.estadoDestino : e.nextStep ? e.nextStep.menu === true ? `aguarda_input_${e.estadoDestino.toLowerCase()}` : e.nextStep.estadoDestino : '',
                  "nextStep": e.nextStep ,
                  "valida": config ? config.nextStep.valida : e.valida,
                }
              }) : config ? `{{resource.${config.nextStep.estadoDestino}_resposta}}` : `{{resource.${estado}}}`,
               
              
              "tipo": config ? config.nextStep.menu !== true ? 'text/plain' : 'application/vnd.lime.select+json': 'application/vnd.lime.select+json',
               "tracking": `${estado}|Input invalido`,
               "time": "",
               "cabecalho": config ? config.nextStep.menu !== true ? `{{resource.${estado}_resposta}}` : `{{resource.${config.nextStep.estadoDestino}_resposta}}`: '{{resource.input_invalido_resposta}}',
               "opcoesDeMenu": config ? config.nextStep.menu !== true ? `{{resource.${estado}}}` : `{{resource.${config.nextStep.estadoDestino}}}` : `{{resource.${estado}}}`,
               "estadoDestino": config ? config.nextStep.menu !== false  ? config.nextStep.estadoDestino : `aguarda_input_${config.nextStep.estadoDestino}`: estado,
               "menu":  config ? config.nextStep.menu : estado ,
               "valida": config ? config.nextStep.valida : '',
               "next": config ? config.nextStep.next : ''
             }

              
               if(type !== 'text/plain') {

                 flow1.respostas.push(flowState);   

               }
      })

       setTimeout(() =>{

         flow1.respostas.forEach((e) => {

           if(e.estadoDestino.includes('aguarda_input')) {

            flow1.respostas.push({
              "estado": e.estadoDestino,
              "inputs": [],
              "inputText": [],
              "resposta": `{{resource.${e.estadoDestino.split('aguarda_input_')[1]}_resposta}}`,
              "tipo": e.menu === true ? 'application/vnd.lime.select+json' : 'text/plain',
              "tracking": e.estadoDestino,
              "time": "",
              "cabecalho":  `{{resource.${e.estadoDestino.split('aguarda_input_')[1]}_resposta}}` ,
              "opcoesDeMenu": `{{resource.${e.estadoDestino.split('aguarda_input_')[1]}}}` ,
              "estadoDestino": e.nextStep ? e.nextStep.estadoDestino : e.estadoDestino.split('aguarda_input_')[1]
            })
           }

           
           if(e.valida) {

             if(e.valida === true){


               flow1.respostas.push({
                 "estado": `${e.estadoDestino}_invalido`,
                 "inputs": [],
                 "inputText": [],
                 "resposta": `{{resource.${e.estadoDestino}_resposta_invalida}}`,
                 "tipo": e.menu === true ? 'application/vnd.lime.select+json' : 'text/plain',
                 "tracking": e.estadoDestino,
                 "time": "",
                 "cabecalho":  `{{resource.${e.estadoDestino}_resposta_invalida}}` ,
                 "opcoesDeMenu": `{{resource.${e.estadoDestino}}}` ,
                 "estadoDestino": e.estadoDestino
               })
             }


           }
           

           if (typeof e.resposta === 'object') {

             e.resposta.forEach((j) => {
                  
                 if(j.estadoDestino.includes('aguarda_input')){

                  flow1.respostas.push({
                    "estado": j.estadoDestino,
                    "inputs": [],
                    "inputText": [],
                    "resposta": j.nextStep ?  `{{resource.${j.nextStep.estadoDestino}_resposta}}`: '',
                    "tipo": j.nextStep ? j.nextStep.menu === true ? 'application/vnd.lime.select+json':"text/plain" : 'application/vnd.lime.select+json',
                    "tracking": j.nextStep ? `${j.nextStep.estadoDestino}` : j.estadoDestino ,
                    "time": "",
                    "cabecalho": j.nextStep ?   `{{resource.${j.nextStep.estadoDestino}_resposta}}` : '' ,
                    "opcoesDeMenu": j.nextStep ?  `{{resource.${j.nextStep.estadoDestino}}}` : '' ,
                    "estadoDestino": j.nextStep ?  `${j.nextStep.estadoDestino}` : j.estadoDestino 
                  })
                 }

       
                 if(j.valida === true){

                   flow1.respostas.push({
                     "estado": `${j.estadoDestino}_invalido`,
                     "inputs": [],
                     "inputText": [],
                     "resposta": `{{resource.${j.estadoDestino}_resposta_invalida}}`,
                     "tipo": j.menu === true ? 'application/vnd.lime.select+json' : 'text/plain',
                     "tracking": j.estadoDestino,
                     "time": "",
                     "cabecalho":  `{{resource.${j.estadoDestino}_resposta_invalida}}` ,
                     "opcoesDeMenu": `{{resource.${j.estadoDestino}}}` ,
                     "estadoDestino": j.estadoDestino
                   })
                 }


              })
           }
         })


        const uniq =  [...new Set(flow1.respostas.map(({estado}) => estado))].map(e => flow1.respostas.find(({estado}) => estado == e));	
          flow1.respostas = uniq;

       const replaceFlow =  JSON.stringify(flow1).toString()
             .replace(/"{{/g, '`{{')
             .replace(/}}"/g, '}}`')
             .replace(/:"`/g, ':`')
             .replace(/`",/g, '`,')
             .replace(/`"}/g, '`}');
       
       setFlowCopy(replaceFlow);


 const  fn  = `function run(estado, input, objetoNegrito, acaoCanal, saudacao, contact, canal, name, flow) {


   try{ 
   const { negrito, negrito2 } = JSON.parse(objetoNegrito);

   if (name) {

           name = capitalizeFirstLetter(name.split(' ')[0]);
   } else {

           name = ''
   }

   let estadoCanal = canal != 'WhatsApp' ? 'solicita_telefone' : 'boas_vindas';   


   const estrutura = ${replaceFlow};

  const novoEstado = estrutura.respostas.filter(e => e.estado === estado);
 const novoEstadoAux = novoEstado

 if (estado === 'solicita_email') {


   if (validaEmail(input)) {


     const obj = estrutura.respostas.filter(e => e.estado === 'solicita_email')[0];
     obj.emailValido = true;
     return obj

   } else {

     const obj = estrutura.respostas.filter(e => e.estado === 'solicita_email_invalido')[0];
     obj.emailValido = false;
     return obj
   }

 }


 if (estado === 'solicita_cpf') {

   if (validaDocumento(input)) {

     const filtroCanal = canal == 'WhatsApp' ? 'solicita_cpf' : 'solicita_cpf'
     const obj = estrutura.respostas.filter(e => e.estado === filtroCanal)[0];
     obj.validaCpf = true;

     return obj
   } else {

     return estrutura.respostas.filter(e => e.estado === 'solicita_cpf_invalido')[0];
   }

 }
 if (estado === 'solicita_telefone') {


   if (validaTelefone(input) !== "TELEFONE INVALIDO") {


     const obj = estrutura.respostas.filter(e => e.estado === 'solicita_telefone')[0];
     obj.telefoneValido = true;
     return obj

   } else {

     const obj = estrutura.respostas.filter(e => e.estado === 'telefone_invalido')[0];
     obj.telefoneValido = false;
     return obj
   }

 }

     if (estado && !novoEstado[0].inputs.includes(input)) {
     let arraySplitado = [];
 
       novoEstado[0].inputText.forEach(e => {
 
         arraySplitado.push({ id: e.split('-')[1], texto: e.split('-')[0] });
       });
 
       const id = arraySplitado.filter(e => e.texto == input);
 
       return id.length > 0 ? novoEstado[0].resposta.filter(e => e.id == id[0].id)[0] : novoEstadoAux[0];
 
   } else {
     return novoEstado[0].inputs.includes(input) ? 
               novoEstado[0].resposta.filter(e => e.id == input)[0] : false;
       
   }

} catch(e) {

   return e;


}
}


function validaDocumento(documento) {

 documento = documento.replace(/\D/g, '');

 return documento.length > 11 ? verifyDoc()["validCnpj"](documento) : verifyDoc()["validCpf"](documento)

}

function verifyDoc(documento1) {
 return {
   validCpf: function (cpf) {
     if (!cpf || cpf.length != 11
       || cpf == "00000000000"
       || cpf == "11111111111"
       || cpf == "22222222222"
       || cpf == "33333333333"
       || cpf == "44444444444"
       || cpf == "55555555555"
       || cpf == "66666666666"
       || cpf == "77777777777"
       || cpf == "88888888888"
       || cpf == "99999999999")
       return false
     var soma = 0
     var resto
     for (var i = 1; i <= 9; i++)
       soma = soma + parseInt(cpf.substring(i - 1, i)) * (11 - i)
     resto = (soma * 10) % 11
     if ((resto == 10) || (resto == 11)) resto = 0
     if (resto != parseInt(cpf.substring(9, 10))) return false
     soma = 0
     for (var i = 1; i <= 10; i++)
       soma = soma + parseInt(cpf.substring(i - 1, i)) * (12 - i)
     resto = (soma * 10) % 11
     if ((resto == 10) || (resto == 11)) resto = 0
     if (resto != parseInt(cpf.substring(10, 11))) return false
     return true
   },
   validCnpj: function (cnpj) {
     if (!cnpj || cnpj.length != 14
       || cnpj == "00000000000000"
       || cnpj == "11111111111111"
       || cnpj == "22222222222222"
       || cnpj == "33333333333333"
       || cnpj == "44444444444444"
       || cnpj == "55555555555555"
       || cnpj == "66666666666666"
       || cnpj == "77777777777777"
       || cnpj == "88888888888888"
       || cnpj == "99999999999999")
       return false
     var tamanho = cnpj.length - 2
     var numeros = cnpj.substring(0, tamanho)
     var digitos = cnpj.substring(tamanho)
     var soma = 0
     var pos = tamanho - 7
     for (var i = tamanho; i >= 1; i--) {
       soma += numeros.charAt(tamanho - i) * pos--
       if (pos < 2) pos = 9
     }
     var resultado = soma % 11 < 2 ? 0 : 11 - soma % 11
     if (resultado != digitos.charAt(0)) return false;
     tamanho = tamanho + 1
     numeros = cnpj.substring(0, tamanho)
     soma = 0
     pos = tamanho - 7
     for (var i = tamanho; i >= 1; i--) {
       soma += numeros.charAt(tamanho - i) * pos--
       if (pos < 2) pos = 9
     }
     resultado = soma % 11 < 2 ? 0 : 11 - soma % 11
     if (resultado != digitos.charAt(1)) return false
     return true;
   }
 }
}

function validaTelefone(phoneText) {
 // var phoneNumber = phoneText.replace(/\D+/g, '');

 // if (phoneNumber.length >= 10 && phoneNumber.length <= 13)
 //   return phoneNumber;
 var RegExp = /^(([1-2][0-8])(9\d{4})\d{4})|(((1[2-9]{1})|([2-9]{1}\d{1}))[5-9]\d{3}\d{4})$/

  if(RegExp.test(phoneText))
    return true
 return "TELEFONE INVALIDO";
}

function validaEmail(emailText) {
 let emailsFound = [];

 var words = emailText.split(' ');

 for (i = 0; i < words.length; i++) {
   var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

   if (regex.test(String(words[i]).toLowerCase())) {
     emailsFound.push(words[i]);
   }
 }

 if (emailsFound.length > 0) {
   return !!emailsFound[0];
 }
 else {
   return false;
 }
}
function capitalizeFirstLetter(string) {
 return string.charAt(0).toUpperCase() + string.slice(1);
}
`

 const handleAddBoxInBuilder = async () => {

   const comeBackHandleGetWorkFlowOrigin =  await  handleGetWorkFlow(guid(), keyOrigin);

   const arrayOfBoxId = Object.keys(comeBackHandleGetWorkFlowOrigin.data.resource).filter((e) => e !== 'onboarding' && e !== 'fallback' && e !== 'error');
   
     arrayOfBoxId.forEach((e) => {

       let result = comeBackHandleGetWorkFlowOrigin.data.resource[e].$enteringCustomActions.filter((e) => e.$title === 'js - Motor RW');

       if(result.length > 0){

         comeBackHandleGetWorkFlowOrigin.data.resource[e].$enteringCustomActions.filter((e) => {

           if(e.$title === 'js - Motor RW') {

             e.settings.source = fn;

           }

         })
         
       }
     });

  const publish  = await handleSetPublishedFlow(comeBackHandleGetWorkFlowOrigin.data.resource, keyOrigin);
  console.log(publish);

   
}


  handleAddBoxInBuilder()
    setFlow(flow1);
    showMessage('Your flow was build with success','success', 'Done!');
    setLoading(false);
  },2000)

 }


function showMessage(msg, type, status) {

setTimeout(() => {
    showToast({
      type: type,
      message: msg
  })
 }, 900);

}


function setLocaStorageKey1(e) {

    setKeyOrigin(e)
    localStorage.setItem('keyOne', e )

 }
 

 function clear() {

   localStorage.removeItem('keyOne');
    setKeyOrigin('');
 }



 const page = (
     
  <div className="fadeIn animated" style={{padding:'30px'}}>
    <div className="home" >
      <div className="" >
        <form className="">
          <div className="form-group">
        
            <input  value={keyOrigin} 
            onChange={(event) => setLocaStorageKey1(event.target.value)} 
            type="text" name="origem" className="bp-input-wrapper blip-input w-100"   
            placeholder="Enter the source bot key." required disabled={true}/>
          </div>
          
         <div className="text-center">
          <button type="submit" onClick={start} style={{marginRight:"10px"}} className="bp-btn bp-btn--bot bp-btn--small">
          {loading && (
            <BlipLoading/>
          )}
          {loading && <span>Create flow</span>}
          {!loading && <span>Create flow</span>}
          </button>
          <button type="button" onClick={clear} style={{marginRight:"10px"}} className="bp-btn bp-btn--bot bp-btn--small">Clear</button>
      
         </div>
        </form><br/>
        <ReactJson src={flow} theme=""  displayDataTypes={false} displayArrayKey={false} quotesOnKeys="false" name="Flow"/>
      
      </div>
    </div>
    <NotificationContainer/>
  </div>
   
 
)
   return (
     <div className="">
       {page}
     </div>
   );
 }

export default Clone;
